import React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../components/layout"
import CourseLayout from "../components/courseLayout"

const CourseHomework = ({ pageContext: { course } }) => (

  <Layout>
  	<CourseLayout>
    	<h2>Homeworks</h2>
    	<p className="pHomework">Each and every homework is listed here</p>

    	{course.homeworks.map(homework => 
    			<div>
    				<h6>Homework #{homework.id}</h6>
    				<p>{homework.description}</p>
    				<li> 
    					<a href={homework.url} target="blank">Info and instructions</a>
    				</li>
    				<li> 
    					<a href={homework.quiz} target="blank">Quiz</a>
    				</li>

                    <p>Max rating points: <b>{homework.points}</b></p>
    			    <p>Deadline: <b>{homework.deadline}</b></p>
    				
    				<hr />
    			</div>
    			)
    	}
    	
    </CourseLayout>
  </Layout>

)

export default CourseHomework

